import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";

import { SidebarContext } from "context/SidebarContext";
import SettingServices from "services/SettingServices";
import { notifyError, notifySuccess } from "utils/toast";
import {
  createCategories,
  createTask,
  createVendor,
  updateCategories,
  updateVendor,
} from "services/Api";
import { useHistory } from "react-router-dom";

const useCategoriesSubmit = (id, getAllCategoriesList, pagenumber) => {
  const { setIsUpdate,toggleDrawer, closeDrawer, } = useContext(SidebarContext);
  const [isSave, setIsSave] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [imageUrl, setImageUrl] = useState([]);
  const [openModal, setOpenModal] = useState(false);
  const history = useHistory();
  const onCloseModal = () => {
    console.log("eff")
    toggleDrawer()
    setOpenModal(false);
    reset()
  }
  const {
    register,
    handleSubmit,
    setValue,
    reset,
    watch,
    formState: { errors },
  } = useForm();

  const onSubmit = async (data) => {
    console.log(data, "datadata");

    try {
      setIsSubmitting(true);
      const payload = {
        name: data?.Name,
      };
      if (id) {
        payload.id = id;
      }

      let res = {};
      if (id) res = await updateCategories(payload);
      else res = await createCategories(payload);
      if (res.data.status == true) {
        notifySuccess(res.data.message);
        reset();
        closeDrawer();
        getAllCategoriesList(pagenumber);
      }
      setIsSubmitting(false);
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
  };

  return {
    errors,
    register,
    handleSubmit,
    onSubmit,
    isSave,
    imageUrl,
    setImageUrl,
    onCloseModal,
    setValue,
    isSubmitting,
    setValue,
    watch,
  };
};

export default useCategoriesSubmit;
