import { Badge, TableBody, TableCell, TableRow } from "@windmill/react-ui";
import DeleteModal from "./DeleteCategories";
import ShowHideButton from "components/table/ShowHideButton";
import Tooltip from "components/tooltip/Tooltip";
import useToggleDrawer from "hooks/useToggleDrawer";
import { t } from "i18next";
import { useContext, useState } from "react";
import { FiEdit, FiTrash2, FiZoomIn } from "react-icons/fi";
import { useHistory } from "react-router-dom";
import { SidebarContext } from "context/SidebarContext";
import moment from "moment";

//internal import

const ProductTable = ({
  products,
  isCheck,
  getAllCategoriesList,
  setIsCheck,
  serviceId,
  pagenumber,
  setserviceId,
  handleUpdate,
  wholeCount,
}) => {
  const { toggleModal } = useContext(SidebarContext);
  const { title } = useToggleDrawer();
  const [mainID, setmainID] = useState("")
  console.log("products", products);
  const handleClick = (e) => {
    const { id, checked } = e.target;

    setIsCheck([...isCheck, id]);
    if (!checked) {
      setIsCheck(isCheck.filter((item) => item !== id));
    }
  };

  const handleModalOpen = (data) => {
    toggleModal();
    // setserviceId(data);
    setmainID(data)
  };

  return (
    <>
      {isCheck?.length < 1 && (
        <DeleteModal
        getAllCategoriesList={getAllCategoriesList}
          id={serviceId}
          pagenumber={pagenumber}
          mainID={mainID}
          title={title}
        />
      )}
      <TableBody>
        {products?.map((properties, i) => (
          <TableRow key={i + 1}>
            <TableCell>
            <span className="text-sm">{(wholeCount?.currentPage-1)*wholeCount?.pageSize+i+1}</span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm">{properties?.name}</span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm">{properties?.slug}</span>
            </TableCell>

            <TableCell className="textcorrect">
              <span className="text-sm">
                {moment(properties?.createdAt).format("DD-MM-YYYY hh:mm")}
              </span>
            </TableCell>
            <TableCell>
              <button
                onClick={() => handleUpdate(properties.id)}
                className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
              >
                <Tooltip
                  id="edit"
                  Icon={FiEdit}
                  title={t("Edit1")}
                  bgColor="#10B981"
                />
              </button>
              <button
                onClick={() => handleModalOpen(properties.id)}
                className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
              >
                <Tooltip
                  id="delete"
                  Icon={FiTrash2}
                  title={t("Delete")}
                  bgColor="#EF4444"
                />
              </button>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default ProductTable;
