import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useAsync from "hooks/useAsync";
import useToggleDrawer from "hooks/useToggleDrawer";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import propertiesformlocal from "../../utils/banners";
import ProductTable from "components/Blogs/blogsCategoriesTable";
import MainDrawer from "components/drawer/MainDrawer";
import useProductFilter from "hooks/useProductFilter";
import TableLoading from "components/preloader/TableLoading";
import SettingServices from "services/SettingServices";
import {
  getAllblogCategoriesListDetails,
  getAllblogCategoriesListDetailswithpagination,
} from "services/Api";
import FAQsDrawer from "components/Blogs/AddCategory";

const BlogCategories = () => {
  const { serviceId, setServiceId, handleUpdate } = useToggleDrawer();

  const { t } = useTranslation();
  const { toggleDrawer, lang, handleSubmitForAll, limitData } =
    useContext(SidebarContext);

  const { data: globalSetting } = useAsync(SettingServices.getGlobalSetting);
  const [isCheck, setIsCheck] = useState([]);
  const [pagenumber, setpagenumber] = useState(1);
  const [loading, setLoading] = useState(false);
  const [BlogCategories, setBlogCategories] = useState([]);
  const [wholeCount, setwholeCount] = useState({});
  const { serviceData } = useProductFilter(propertiesformlocal);

  const getAllCategoriesList = async (pagenumber, search = "") => {
    const Categorieslist = await getAllblogCategoriesListDetailswithpagination(
      pagenumber,
      search
    );
    console.log(Categorieslist?.data?.data, "Categorieslist");
    setLoading(false);
    setBlogCategories(Categorieslist?.data?.data);
    setwholeCount(Categorieslist?.data?.data?.pagination);
  };
  const handleChangePage = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getAllCategoriesList(e);
  };
  console.log(serviceId, "serviceId");
  useEffect(() => {
    setLoading(true);
    getAllCategoriesList(pagenumber);
  }, []);

  const handleSearch = (search) => {
    console.log(search, "search");
    getAllCategoriesList(pagenumber, search);
  };
  return (
    <>
      <PageTitle>{t("Categories List")}</PageTitle>
      <MainDrawer>
        <FAQsDrawer
          pagenumber={pagenumber}
          getAllCategoriesList={getAllCategoriesList}
          id={serviceId}
        />
      </MainDrawer>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Button
                  onClick={toggleDrawer}
                  className="w-full rounded-md h-12"
                >
                  <span className="mr-2">
                    <FiPlus />
                  </span>
                  {t("Add Categories")}
                </Button>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell className="text-left">{"s.no"}</TableCell>
                <TableCell>{t("Name")}</TableCell>
                <TableCell>{t("Slug")}</TableCell>
                <TableCell>{t("Created At")}</TableCell>
                <TableCell className="text-left">{t("Action")}</TableCell>
              </tr>
            </TableHeader>
            <ProductTable
              lang={lang}
              isCheck={isCheck}
              products={BlogCategories?.blogCategories}
              getAllCategoriesList={getAllCategoriesList}
              setIsCheck={setIsCheck}
              pagenumber={pagenumber}
              serviceId={serviceId}
              setserviceId={setServiceId}
              handleUpdate={handleUpdate}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={BlogCategories?.pagination?.total}
              resultsPerPage={BlogCategories?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default BlogCategories;
