import { Input, Textarea, Select } from "@windmill/react-ui";
import DrawerButton from "components/form/DrawerButton";
import Error from "components/form/Error";
import LabelArea from "components/form/LabelArea";
import Title from "components/form/Title";
import { t } from "i18next";
import { Scrollbars } from "react-custom-scrollbars-2";
import { useEffect, useState } from "react";
import useTaskSubmit from "hooks/useAddCategories";
import { getCategoryById } from "services/Api";

const FAQsDrawer = ({ id, getAllCategoriesList, pagenumber }) => {
  const {
    register,
    handleSubmit,
    onSubmit,
    imageUrl,
    setImageUrl,
    onCloseModal,
    errors,
    handleProductSlug,
    setValue,
    isSubmitting,
    handleSelectLanguage,
  } = useTaskSubmit(id, getAllCategoriesList, pagenumber);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
  }, []);

  useEffect(() => {
    if (id) {
      getSingleCategories(id);
    }
  }, [id]);
  const getSingleCategories = async (id) => {
    const Categories = await getCategoryById(id);
    if (Categories && Categories.status === 200) {
        console.log(Categories,"Categories")
      setValue("Name", Categories?.data?.data?.blogCategory?.name);
    }
  };

  return (
    <>
      <div className="w-full relative  p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300 ">
        {id ? (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Edit Categories")}
            description={t("Edit Categories Here")}
          />
        ) : (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Add Categories")}
            description={t("Add Categories Here")}
          />
        )}
      </div>

      <Scrollbars className="w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="px-6 pt-8 flex-grow scrollbar-hide w-full max-h-full pb-40">
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Name")} />
              <div className="col-span-8 sm:col-span-4">
                <Input
                  {...register(`Name`, {
                    required: "Name required!",
                  })}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                  name="Name"
                  type="text"
                  placeholder={t("Enter Name ")}
                  onBlur={(e) => handleProductSlug(e.target.value)}
                />
                <Error errorName={errors.Name} />
              </div>
            </div>
          </div>

          <DrawerButton imageUrl={imageUrl} setImageUrl={setImageUrl} onCloseModal={onCloseModal} id={id} title="Category" isSubmitting={isSubmitting} />
        </form>
      </Scrollbars>
    </>
  );
};

export default FAQsDrawer;
